      let data = null      
      let layout = this.item ? this.item.layout : null      
      if (layout === ENUM.CARDGROUP_LAYOUT.B1L) {                
        data = this.b1lItems
      } else if (layout === ENUM.CARDGROUP_LAYOUT.B1R) {                
