<template>
  <div>
    <mt-header class="header" title="优惠活动">
      <header-item slot="left" v-bind:isBack = true v-on:onclick="goBack">
      </header-item>    
    </mt-header> 
    <div infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="discount-body">
      <discount-body v-for="(item, index) in activitiesList" :index="index" :item="item" :key="index"></discount-body>
      <div class="wrapper-list-empty" v-if='activitiesList.length <= 0'>
		<div>
			<img src="../../assets/image/change-icon/empty_active@2x.png">
			<p>暂无优惠活动</p>
		</div>
	  </div>
	</div>
  </div>
</template>

<script>
import { HeaderItem } from '../../components/common'
import { Header } from 'mint-ui'

import DiscountBody from './child/DiscountBody';
import { activityList } from '../../api/activity'; //优惠活动列表

export default {
  components: {
    DiscountBody,
  },
  data() {
    return {
      activitiesList: [],
    };
  },
  created() {
    this.getActivityList();
  },
  methods: {  
    goBack() {
      this.$router.go(-1); 
    },
    // 获取订单列表
    getActivityList() {
      activityList().then(res => {
        if (res) {
          this.activitiesList = res.activities;
        }
      });
    },
  }
}
</script>

<style lang="scss" scoped>

.header {
  @include header;
  background-color: #ffffff;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  border-bottom: 1px solid $lineColor;
}
.discount-body {
  position: absolute;
  top: 44px;
  bottom: 0px;
  overflow-y: auto;
  width: 100%;
}
.wrapper-list-empty {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  padding-top: 40%;
  div {
  	display: flex;
  	flex-direction: column;
  	align-items: center;
  	img {
  		width: 75px;
  		height: 75px;
  	}
    p {
    	margin-top: 27px;
      text-align: center;
      color: #A4AAB3;
    }
  }
}

</style>



